import React, { useCallback, useEffect, useState } from "react";
import Button from "../../../components/Button";
import RevisionView from "./RevisionView";
import { RevisionAcceptAndContinue } from "./RevisionAcceptAndContinue";
import AssigneeRevisionToDev from "./AssigneeRevisionToDev";
import DenyAndContinue from "./DenyAndContinue";
import {
    useGetRevisionDetailsQuery,
    useRevisionAcceptOrDenyByLeadDeveloperMutation,
    useRevisionAcceptOrDenyMutation,
} from "../../../../services/api/SingleTaskPageApi";
import { useDispatch, useSelector } from "react-redux";
import { setTaskStatus } from "../../../../services/features/subTaskSlice";
import _ from "lodash";
import { User } from "../../../../utils/user-details";

const RevisionViewModal = ({ task, close }) => {
    const { task: Task } = useSelector((s) => s.subTask);
    const [show, setShow] = useState("REVISION");
    const [accept, setAccept] = useState("");
    const [comment, setComment] = useState("");
    const [denyReason, setDenyReason] = useState("");
    const dispatch = useDispatch();
    const { data: revision, isFetching } = useGetRevisionDetailsQuery(task?.id);
    //   const [revisionAcceptOrDeny, {isLoading: isLoadingRevisionReview}] = useRevisionAcceptOrDenyMutation();
    const auth = new User(window?.Laravel?.user);
    const [revisionAcceptOrDeny, { isLoading: isLoadingRevisionReview }] =
        useRevisionAcceptOrDenyByLeadDeveloperMutation();

    // useEffect(()=>{
    //   console.log({Task,name:Task?.create_by?.name});
    // },[Task])

    // handle Accept and continue submition
    const hanldeAcceptAndContinueSubmition = (data, type) => {
        setComment(data);
        setShow(type);
    };

    // handle Accept and continue submition
    const hanldeDenyAndContinueSubmition = (data, type) => {
        setComment(data);
        setDenyReason(data?.denyReason);
        setShow(type);
    };

    const handleOnSubmit = (data, type) => {
        let fdata = {
            comment: comment?.comment ?? "",
            task_id: data?.task_id,
            project_id: task?.projectId,
            user_id: auth?.getId(),
            subTask: _.map(data?.comments, (comment) => ({
                ...comment,
                is_deniable: data?.is_deniable,
            })),
            revision_acknowledgement: data?.reason ?? "",
            revision_id: revision?.id,
            mode: data?.continue ? "continue" : accept,
            deny_reason: denyReason ?? "",
            is_deniable: data?.is_deniable ?? false,
        };

        const params =
            !data?.continue && accept === "deny"
                ? "deny-continue"
                : "accept-continue";

        revisionAcceptOrDeny({ fdata, params })
            .unwrap()
            .then((res) => {
                close();
            })
            .catch((err) => console.log(err));
    };

    const handleContinueButton = () => {
        console.log(revision);
        setAccept("continue");
        if (_.size(revision?.taskSubTask) === 0) {
            setShow("DENY_ASSIGNEE_TO_DEV");
            close();
        } else {
            handleOnSubmit(
                {
                    continue: true,
                },
                ""
            );
        }
    };

    const generateModalTitle = useCallback(() => {
        if (auth.getRoleId() === 4) {
            return show === "ASSIGNEE_TO_DEV"
                ? "Revision For Lead Developer"
                : `Revision By ${Task?.create_by?.name}`;
        } else if (auth.getRoleId() === 6) {
            return show === "ASSIGNEE_TO_DEV"
                ? "Revision For Developer"
                : `Revision By ${Task?.create_by?.name}`;
        } else if (auth.getRoleId() === 9 || auth.getRoleId() === 10) {
            return `Revision By ${Task?.create_by?.name}`;
        } else return "Revision By Lead Developer";
    }, [Task]);

    //   console.log({task})
    return (
        <React.Fragment>
            <div
                className="sp1_single_task--modal-panel"
                style={{ maxWidth: "550px" }}
            >
                <div className="border-bottom pb-2 px-3 mb-3 d-flex align-items-center justify-content-between">
                    <div className="font-weight-bold f-16">
                        Task#{task?.id}:{generateModalTitle()}
                    </div>
                    <Button onClick={close} className="">
                        <i className="fa-solid fa-xmark" />
                    </Button>
                </div>

                <div className="px-3">
                    {/* revision view */}
                    {show === "REVISION" && (
                        <RevisionView
                            revision={revision}
                            isLoading={isFetching}
                            isContinue={isLoadingRevisionReview}
                            onAccept={() => {
                                setAccept("accept");
                                setShow("ACCEPT_AND_CONTINUE");
                            }}
                            onDeny={() => {
                                setAccept("deny");
                                setShow("DENY_AND_CONTINUE");
                            }}
                            onContinue={handleContinueButton}
                        />
                    )}

                    {/* revision accepted after view */}
                    {show === "ACCEPT_AND_CONTINUE" && (
                        <RevisionAcceptAndContinue
                            task={task}
                            isSubmitting={isLoadingRevisionReview}
                            onSubmit={(data) =>
                                hanldeAcceptAndContinueSubmition(
                                    data,
                                    "ASSIGNEE_TO_DEV"
                                )
                            }
                            close={() => setShow("REVISION")}
                        />
                    )}

                    {/* revision assigned to dev after accepted */}
                    {_.size(task?.taskSubTask) > 0 &&
                        show === "ASSIGNEE_TO_DEV" && (
                            <AssigneeRevisionToDev
                                task={task}
                                revision={revision}
                                type={true}
                                onSubmit={(data) =>
                                    handleOnSubmit(data, "ASSIGNEE_TO_DEV")
                                }
                                isSubmitting={isLoadingRevisionReview}
                                onBack={() => setShow("ACCEPT_AND_CONTINUE")}
                            />
                        )}

                    {/* revision denied after view */}
                    {show === "DENY_AND_CONTINUE" && (
                        <DenyAndContinue
                            task={task}
                            onSubmit={(data) =>
                                hanldeDenyAndContinueSubmition(
                                    data,
                                    "DENY_ASSIGNEE_TO_DEV"
                                )
                            }
                            isSubmitting={isLoadingRevisionReview}
                            onBack={() => setShow("REVISION")}
                        />
                    )}

                    {/* revision assigned to dev after denied */}
                    {_.size(task?.taskSubTask) > 0 &&
                        show === "DENY_ASSIGNEE_TO_DEV" && (
                            <AssigneeRevisionToDev
                                task={task}
                                revision={revision}
                                type={false}
                                onSubmit={(data) =>
                                    handleOnSubmit(data, "DENY_ASSIGNEE_TO_DEV")
                                }
                                isSubmitting={isLoadingRevisionReview}
                                onBack={() => setShow("DENY_AND_CONTINUE")}
                            />
                        )}
                </div>
            </div>
        </React.Fragment>
    );
};

export default RevisionViewModal;
